import "core-js/modules/es.array.push.js";
export default {
  data() {
    return {
      Number: 0,
      showLogoutButton: false,
      tableData: [],
      search: '',
      currentPage: 1,
      pageSize: 15,
      selectedFiles: [],
      sortOption: 'dateDesc',
      isUploading: false
    };
  },
  computed: {
    filteredTableData() {
      return this.tableData.filter(data => !this.search || data.name.toLowerCase().includes(this.search.toLowerCase()));
    },
    paginatedTableData() {
      const sortedData = this.sortData(this.filteredTableData);
      const start = (this.currentPage - 1) * this.pageSize;
      const end = start + this.pageSize;
      return sortedData.slice(start, end);
    },
    sortIcon() {
      return this.sortOption === 'dateDesc' ? 'sort-amount-down' : 'sort-alpha-up';
    },
    previewSrcList() {
      // 过滤掉视频文件，保留非视频文件的路径
      return this.paginatedTableData.filter(file => !file.metadata?.FileType?.includes('video')) // 过滤出非视频文件
      .map(file => `/file/${file.name}`); // 返回文件路径数组
    }
  },
  watch: {
    tableData: {
      handler(newData) {
        this.selectedFiles = newData.filter(file => file.selected);
      },
      deep: true
    },
    sortOption(newOption) {
      localStorage.setItem('sortOption', newOption);
    }
  },
  methods: {
    refreshDashboard() {
      location.reload();
    },
    handleDelete(index, key) {
      this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        fetch(`./api/manage/delete/${key}`, {
          method: 'GET',
          credentials: 'include'
        }).then(response => {
          if (response.ok) {
            const fileIndex = this.tableData.findIndex(file => file.name === key);
            if (fileIndex !== -1) {
              this.tableData.splice(fileIndex, 1);
            }
          } else {
            return Promise.reject('请求失败');
          }
        }).then(() => {
          this.updateStats();
          this.$message.success('删除成功!');
        }).catch(() => this.$message.error('删除失败，请检查网络连接'));
      }).catch(() => this.$message.info('已取消删除'));
    },
    handleBatchDelete() {
      this.$confirm('此操作将永久删除选中的文件, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        const promises = this.selectedFiles.map(file => fetch(`./api/manage/delete/${file.name}`, {
          method: 'GET',
          credentials: 'include'
        }));
        Promise.all(promises).then(results => {
          results.forEach((response, index) => {
            if (response.ok) {
              const fileIndex = this.tableData.findIndex(file => file.name === this.selectedFiles[index].name);
              if (fileIndex !== -1) {
                this.tableData.splice(fileIndex, 1);
              }
            }
          });
          this.selectedFiles = [];
          this.updateStats();
          this.$message.success('批量删除成功!');
        }).catch(() => this.$message.error('批量删除失败，请检查网络连接'));
      }).catch(() => this.$message.info('已取消批量删除'));
    },
    handleBatchCopy() {
      const links = this.selectedFiles.map(file => `${document.location.origin}/file/${file.name}`).join('\n');
      navigator.clipboard ? navigator.clipboard.writeText(links).then(() => this.$message.success('批量复制链接成功~')) : this.copyToClipboardFallback(links);
    },
    copyToClipboardFallback(text) {
      const textarea = document.createElement('textarea');
      document.body.appendChild(textarea);
      textarea.style.position = 'fixed';
      textarea.style.clip = 'rect(0 0 0 0)';
      textarea.style.top = '10px';
      textarea.value = text;
      textarea.select();
      document.execCommand('copy');
      document.body.removeChild(textarea);
      this.$message.success('批量复制链接成功~');
    },
    handleLogout() {
      this.$router.push('/');
    },
    handleGoToAdmin() {
      window.location.href = '/admin-detail';
    },
    handleCopy(index, key) {
      const text = `${document.location.origin}/file/${key}`;
      navigator.clipboard ? navigator.clipboard.writeText(text).then(() => this.$message.success('复制文件链接成功~')) : this.copyToClipboardFallback(text);
    },
    handlePageChange(page) {
      this.currentPage = page;
    },
    updateStats() {
      this.Number = this.tableData.length;
    },
    sort(command) {
      this.sortOption = command;
    },
    sortData(data) {
      return this.sortOption === 'nameAsc' ? data.sort((a, b) => a.name.localeCompare(b.name)) : data.sort((a, b) => b.metadata.TimeStamp - a.metadata.TimeStamp);
    },
    handleVideoClick(event) {
      const videoElement = event.target;
      if (videoElement.requestFullscreen) {
        videoElement.requestFullscreen();
      } else if (videoElement.webkitRequestFullscreen) {
        // Safari/Old Chrome
        videoElement.webkitRequestFullscreen();
      } else if (videoElement.mozRequestFullScreen) {
        // Firefox
        videoElement.mozRequestFullScreen();
      } else if (videoElement.msRequestFullscreen) {
        // IE/Edge
        videoElement.msRequestFullscreen();
      }
    }
  },
  mounted() {
    fetch("/api/manage/check", {
      method: 'GET',
      credentials: 'include'
    }).then(response => response.text()).then(result => {
      if (result == "true") {
        this.showLogoutButton = true;
        // 在 check 成功后再执行 list 的 fetch 请求
        return fetch("/api/manage/list", {
          method: 'GET',
          credentials: 'include'
        });
      } else if (result == "Not using basic auth.") {
        return fetch("/api/manage/list", {
          method: 'GET',
          credentials: 'include'
        });
      } else {
        // window.location.reload();
        return Promise.reject();
      }
    }).then(response => response.json()).then(result => {
      this.tableData = result.map(file => ({
        ...file,
        selected: false
      }));
      this.updateStats();
      const savedSortOption = localStorage.getItem('sortOption');
      if (savedSortOption) {
        this.sortOption = savedSortOption;
      }
      this.sortData(this.tableData);
    }).catch(() => this.$message.error('同步数据时出错，请检查网络连接'));
  }
};